@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  min-height: 100vh;
}

#root {
  min-height: 100vh;
}

.container {
  min-height: 100%;
  background: #262626;
  text-align: center;
  color: #262626;
}

.profile img {
  border-radius: 50%;
  width: 200px;
  margin-top: 15px;
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
}

.profile img:hover {
  -webkit-filter: brightness(130%);
          filter: brightness(130%);
}

.profile {
  width: 240px;
  margin: 0 auto;
}

.profile-box {
  will-change: width, height, left, top;
  position: absolute;
}

.introduction {
  margin: auto;
  text-align: center;
  position: relative;
  background: transparent;
  display: block;
}

.highlights img {
  border-radius: 0;
  display: inline-block;
  width: 40px;
  position: absolute;
  z-index: 150;
}

h1 {
  font-size: 30px;
  margin: auto;
  display: inline;
  font-family: 'Open Sans';
  cursor: default;
}

h2 {
  font-size: 30px;
  margin: auto;
  display: inline;
  font-family: 'Open Sans';
  cursor: default;
}

h3 {
  font-size: 30px;
  margin: auto;
  display: inline;
  font-family: 'Open Sans';
  z-index: 100;
  cursor: default;
}

h4 {
  display: block;
  font-size: 15px;
  z-index: 100;
  margin: 0;
  font-family: 'Montserrat';
  cursor: default;
}

h5 {
  display: block;
  position: relative;
  margin: 0 auto;
  font-family: 'Open Sans';
  font-size: 20px;
}

p {
  display: block;
  position: relative;
  margin: 0 auto;
  padding: 10px 0;
  max-width: 400px;
  z-Index: 50;
}
div {
    text-align: center;
}

table {
    position: relative;
    top: -30px;
    margin: 0 auto;
    table-layout: fixed;
    font-family: 'Open Sans';
}

td {
    padding: 0 20px;
}

a svg {
    width: 50px;
}

a svg:hover > path {
    fill: #6a2c70;
}

.skills {
    margin: 0 auto;
}

.skills td {
    padding: 15px 5px 5px 5px;
    max-width: 100px;
}

.row2 td{
    padding: 0 5px 15px 5px;
    top: -30px;
}

.projects img {
    max-width: 100px;
    border-radius: 15px;
    margin: 6px 0;
}

.projects img:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}

.projects td {
    max-width: 300px;
    padding: 0 8px;
    font-size: 13px;
    text-align: left;
}
